  color: #fff;
  justify-content: center;
  flex-direction: column;
}



/* VERTICALLYTG CENTRE */
.isitweeka {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 10vw;
  margin-left: var(--horizontal-indent);
  margin-right: 10vw;
  margin-right: var(--horizontal-indent);
}

.isitweeka h1 {
  font-size: 7em;
  margin-top: 0;
  margin-bottom: 0;
  display: inline-flex;
}
.isitweeka h2 {
  font-size: 3em;
  display: inline-flex;
}
.isitweeka h3 {
  font-size: 2.5em;
  display: inline-flex;
}

.isitweeka h4 {
  font-size: 1.5em;
  display: inline-flex;
}

.isitweeka h5 {
  font-size: 1em;
  display: inline-flex;
}

.isitweeka-jumbotron a {
  color: #00B0FF;
  display: inline-flex;
}

.events * {
  text-align: left;
}

/* Button */

button {
  color: inherit;
  font-size: 1.5em;
  font-weight: 700;
  line-height: 26px;
  text-transform: lowercase;
  text-decoration: none;
  transition-duration: 100ms;
  position: relative;
  cursor: pointer;
  display: inline-flex;
  flex-direction: row;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  background: transparent;
  border: none;
  padding-top: 3px;
}

button > span {
  z-index: 2;
}

button.forward {
  /*margin-right: auto;*/
  /*margin-left: -8px;*/
  width: auto;
}

button.back {
  height: 100%;
  justify-content: center;
  flex-direction: column;
}

button.forward::after {
  content: url(/static/media/appbar.arrow.right.13c901ab.svg);
  /*content: url(#icon);*/
  width: 48px;
  height: 48px;
  margin: -8px;
  padding-left: 8px;
  fill: white;
  fill: var(--icon-fill);
  -webkit-filter: invert(100%) sepia(0%) saturate(7500%) hue-rotate(130deg) brightness(119%) contrast(116%);
          filter: invert(100%) sepia(0%) saturate(7500%) hue-rotate(130deg) brightness(119%) contrast(116%);
}

button.back::before {
  content: url(/static/media/appbar.arrow.left.1c36cdf2.svg);
  /*content: url(#icon);*/
  width: 48px;
  height: 48px;
  margin: -8px;
  /*margin-left: -24px;*/
  padding-right: 16px;
  fill: white;
  fill: var(--icon-fill);
  -webkit-filter: invert(100%) sepia(0%) saturate(7500%) hue-rotate(130deg) brightness(119%) contrast(116%);
          filter: invert(100%) sepia(0%) saturate(7500%) hue-rotate(130deg) brightness(119%) contrast(116%);
}

button.forward::before {
  content: "";
  /* background-image: linear-gradient(to right, var(--rykan-accent-primary), var(--rykan-accent-secondary)); */
  background-color: #F10DA2;
  background-color: var(--rykan-accent-primary);
  position: absolute;
  height: calc(100% + 8px);
  width: calc(100% + 8px);
  left: -4px;
  bottom: -4px;
  transition-duration: 100ms;
  pointer-events: none;
  transform: scaleY(0.1);
  transform-origin: 0 100%;
  z-index: 0;
}

button.forward.a::before {
  transform: scaleY(0.1);
}

button.forward.b::before {
  transform: scaleY(1);
}

button.forward:hover::before {
  transform: scaleY(1);
}

button.forward.b:hover::before {
  -webkit-filter: saturate(75%);
          filter: saturate(75%);
}

button.back::after {
  content: "";
  /* background-image: linear-gradient(to right, var(--rykan-accent-primary), var(--rykan-accent-secondary)); */
  background-color: #F10DA2;
  background-color: var(--rykan-accent-primary);
  position: absolute;
  height: 3px;
  width: 100%;
  left: 0;
  bottom: -4px;
  transition-duration: 100ms;
  pointer-events: none;
  transform: scaleX(0);
  transform-origin: 0 0;
}



.events-list {
  display: flex;
  height: 50%;
  flex-direction: column;
}

.events-list > * > div {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
}

.events-row {
  display: flex;
  width: 100%;
  min-height: 320px;
  border-bottom-style: solid;
  border-bottom-width: 1px;
  border-bottom-color: rgba(255, 255, 255, 0.6);
  border-bottom-color: var(--link-active);
  margin: 0 -10vw;
  padding: 0 10vw;
  padding: 0 var(--horizontal-indent);
}

.events-row:first-child {
  border-top-style: solid;
  border-top-width: 1px;
  border-top-color: rgba(255, 255, 255, 0.6);
  border-top-color: var(--link-active);
}



:root {
  --rykan-accent-primary: #F10DA2;
  --rykan-accent-secondary: #FE5000;
  background: black;
  color: white;
  --icon-fill: white;
  --link-active: rgba(255, 255, 255, 0.6);
  --horizontal-indent: 10vw;
}



a {
  color: inherit;
  text-decoration: none;
}



.r-tab-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
}

.r-tab-row {
  display: flex;
  width: 100%;
  justify-content: center;
  /*height: 40px;*/
  padding: 68px 24px 0;
  box-sizing: border-box;
  position: absolute;
  grid-gap: 12px;
  gap: 12px;
}

.r-tab.active::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  height: 4px;
  background-color: #F10DA2;
  background-color: var(--rykan-accent-primary);
}

.r-tab {
  font-size: 2em;
  padding-bottom: 8px;
  padding-top: 3px;
  height: 40px;
}

